#homepage{
    nav{
        margin-top: 1rem;
        #logoid{
            display: flex;
            align-items: center;
            justify-content: flex-start;
            h1{
                text-decoration: none;
                font-size: 1.3rem;
                font-family: 'Allerta Stencil', sans-serif;
                font-weight: 400;
            }
        }
        #navlinkid{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            #productLink, #aboutLink, #contactLink{
                margin-right: 1rem;
                text-decoration: none;
                font-family: 'Allerta Stencil', sans-serif;
                color: $thirty;
                }
        }
    }
    #designText{
        display: flex;
        align-items: center;
        justify-content: center;
        // border: 2px solid red;
        margin-top: 1.8rem;
        position: relative;
        z-index: 2;
        background: transparent;
        h1{
            background: transparent;
            font-family: 'Laila', sans-serif;
        }

    }
    #mainImg{
        #mainMain{
            background: url(./Images/pex.jpg);
            width: 100%;
            min-height: 35rem;
            background-repeat: no-repeat;
            background-attachment: fixed;
            background-size: cover;
            transform: translateY(-2rem);
            z-index: 0;
            position: relative;
        }
    }
    #shot{
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 1rem;
        font-weight: 500;
    }
    #moverr{
        background-color: $thirty;
        color: $sixty;
        
        #holder{
            margin-top: 2rem;
            background-color: $thirty;
            color: $sixty;
            display: flex;
            #moverOne{
                background-color: $thirty;
                color: $sixty;
                h3{
                    background-color: $thirty;
                    color: $sixty;
                    padding: 1rem;
                }
            }
            #moverTwo{
                background-color: $thirty;
                color: $sixty;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                
                div{
                    width: 100%;
                    text-align: end;
                    background: $thirty;
                    button{
                        width: 2rem;
                        height: 2rem;
                        border-radius: 50%;
                        margin-right: 1rem;
                        border: 1px solid $sixty;
                        background: $thirty;
                        transition: transform .3s ease-in-out;
                        margin-bottom: 1rem;
                        i{
                            background: $thirty;
                            color: $sixty
                        }
                        &:hover{
                            transform: scale(1.5);
                        }
                        &:focus{
                            background: $sixty;
                            color: $thirty;
                            i{
                                background: $sixty;
                                color: $thirty;
                            }
                        }
                    }
                }
                }
        }
        #movingImages{
            background-color: $thirty;
            color: $sixty;
            margin-bottom: 2rem;
        }
    }
    #ways{
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin-top: 1.8rem;
        h2{
            width: 30rem;
            font-weight: 600;
            font-size: 2rem;
            text-decoration: underline;
        }
    }
    #waysProp{
        #theFirst{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            margin-top: 2rem;
            padding: 1rem;
            i{
             font-size: 5rem;   
             margin-bottom: .8rem;
             color: $thirty;
            }
            h1{
                font-size: 2rem;
            }
            h4{
                font-size: 1rem;
                text-align: center;
                width: 18rem;
            }
        }
        #theSecond{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            margin-top: 2rem;
            padding: 1rem;
            i{
                font-size: 5rem;
                margin-bottom: .8rem;
                color: $thirty;
            }
            h1{
                font-size: 2rem;
            }
            h4{
                font-size: 1rem;
                text-align: center;
                width: 18rem;
            }
        }
        #theThird{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            margin-top: 2rem;
            padding: 1rem;
            i{
                font-size: 5rem;
                margin-bottom: .8rem;
                color: $thirty;
            }
            h1{
                font-size: 2rem;
            }
            h4{
                font-size: 1rem;
                text-align: center;
                width: 18rem;
            }
        }
    }
    #locate{
        display: flex;
        justify-content: space-around;
        align-items: center;
        #locateText{
            display: flex;
            align-items: center;
            justify-content: center;
            h1{
                text-transform: uppercase;
                font-size: 3.5rem;
            }
        }
    }
    #hello{
        background: $thirty;
        #closeToFooter{
            padding: 1.5rem;
            background: $thirty;
            h1{
                background: $thirty;
                color: $ten;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 3.2rem;
                text-transform: uppercase;
                margin-top: 2rem;
            }
            h4{
                background: $thirty;
                color: $ten;
                font-size: 1.5rem;
                margin-bottom: 1rem;
                margin-top: 2rem;
            }
            div{
                background: $thirty;
                color: $ten;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                width: 90%;
                margin-bottom: 2rem;
                h1{
                    transform: rotate(20deg);
                    border: 1px solid $sixty;
                    border-radius: 50%;
                    padding: 1rem;
                    text-align: center;
                    font-size: 1.5rem;
                    width: 15rem;
                    height: 6rem;
                }
            }
        }
    }
    footer{
        border-top: 1px solid $sixty;
        div{
            // border-top: 1px solid $sixty;
            background: $thirty;
            h3{
            font-family: 'Allerta Stencil', sans-serif;
            display: flex;
            align-items: center;
            background-color: $thirty;
            }
            div{
                border: none;
                // display: flex;
                // align-items: center;
                // flex-direction: column;
                // justify-content: flex-start;
                #one{
                    margin-top: 1rem;
                    text-align: right;
                    a{
                        background: $sixty;
                        margin-right: 1rem;
                        i{
                            font-size: 1.8rem;
                            background: $sixty;
                            color: $thirty;
                            padding: .2rem;
                        }
                    }
                    #tweeta{
                        background: transparent;
                        #tweet{
                            color: $sixty;
                            background: $thirty;
                            padding: .2rem;
                        }
                }
                }
                #two{
                    text-align: right;
                    font-weight: 500;
                    margin-top: 1rem;
                    }
            }
        }
    }
}