#aboutNav{
    nav{
        margin-top: 1rem;
        #Logoid{
            display: flex;
            align-items: center;
            justify-content: flex-start;
            h1{
                text-decoration: none;
                font-size: 1.3rem;
                font-family: 'Allerta Stencil', sans-serif;
                font-weight: 400;
            }
        }
        #navlinkid{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            #productLink, #aboutLink, #contactLink{
                margin-right: 1rem;
                text-decoration: none;
                font-family: 'Allerta Stencil', sans-serif;
                color: $thirty;
                }
        }
    }
    #upfront{
        margin-top: 2rem;
        overflow: hidden;
        #back{
            position: relative;
            // border: 2px solid red;
            display: flex;
            align-items: center;
            justify-content: center;
            #backdrop{
                font-size: 1000%;
                text-transform: uppercase;
                text-align: center;
                // border: 2px solid red;
                opacity: .1;
            }
            #txt{
                position: absolute;
                top: 40%;
                background: transparent;
                // border: 2px solid red;
                #abouttext{
                    // border: 2px solid red;
                    background: transparent;
                    font-size: 3.5rem;
                }
            }
        }
        
    }
    #aboutText{
        font-size: 1.3rem;
        div{
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            border: 2px dashed $thirty;
            width: 20rem;
            height: 20rem;
            // transform: translateX(35vw);
            float: right;
            shape-outside: circle(50%);
            margin: 2rem;
            background: url(https://images.pexels.com/photos/154161/pexels-photo-154161.jpeg?auto=compress&cs=tinysrgb&w=600);
            background-position: center;
            background-size: contain;
            background-attachment: fixed;
        }
    }
    footer{
        margin-top: 1rem;
        border: 2px solid $thirty;
       
        div{
            border-top: 1px solid $sixty;
            background: $sixty;
            h3{
            font-family: 'Allerta Stencil', sans-serif;
            display: flex;
            align-items: center;
            background-color: $sixty;
            }
            div{
                border: none;
                // display: flex;
                // align-items: center;
                // flex-direction: column;
                // justify-content: flex-start;
                #one{
                    margin-top: 1rem;
                    text-align: right;
                    a{
                        background: $sixty;
                        margin-right: 1rem;
                        i{
                            font-size: 1.8rem;
                            background: $sixty;
                            color: $thirty;
                        }
                    }
                    #tweeta{
                        background: transparent;
                        #tweet{
                            color: $sixty;
                            background: $thirty
                        }
                    }
                }
                #two{
                    text-align: right;
                    font-weight: 500;
                }
            }
        }
    }
}