#accordionFlushExample{
    margin-bottom: 5rem;
    margin-top: 1.5rem;
    div{
        background: $sixty;
        color: $ten;
        h2{
           button{
            background: $sixty;
            color: $thirty;
            border-bottom: 1px solid $thirty;
           }
        }
        #holder{
            display: flex;
            align-items: center;
            justify-content: space-between;
            img{
                width: 10rem;
                border-radius: 10px;
                margin-right: .6rem;
                object-fit: contain;
            }
        }
    }
}