#loader {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #faebd7;
}
#loader #cont {
  position: relative;
  display: flex;
  background: transparent;
}
#loader #cont span {
  width: 50px;
  height: 300px;
  margin: 0 20px;
  background: #faebd7;
  -webkit-animation: animate 1.8s linear infinite;
          animation: animate 1.8s linear infinite;
  border-radius: 30px;
}
#loader #cont span:nth-child(1) {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
#loader #cont span:nth-child(2) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}
#loader #cont span:nth-child(3) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}
#loader #cont span:nth-child(4) {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}
#loader #cont span:nth-child(5) {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}
#loader #cont span:nth-child(6) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
#loader #cont span:nth-child(7) {
  -webkit-animation-delay: 1.2s;
          animation-delay: 1.2s;
}
@-webkit-keyframes animate {
  0% {
    box-shadow: 0 0 0 rgba(0, 0, 0, 0.5);
    opacity: 0;
    transform: translateX(-50px) scale(1);
  }
  50% {
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5);
    opacity: 1;
    transform: translateX(0px) scale(1.2);
  }
  100% {
    box-shadow: 0 0px 0px rgba(0, 0, 0, 0.5);
    opacity: 0;
    transform: translateX(50px) scale(1);
  }
}
@keyframes animate {
  0% {
    box-shadow: 0 0 0 rgba(0, 0, 0, 0.5);
    opacity: 0;
    transform: translateX(-50px) scale(1);
  }
  50% {
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5);
    opacity: 1;
    transform: translateX(0px) scale(1.2);
  }
  100% {
    box-shadow: 0 0px 0px rgba(0, 0, 0, 0.5);
    opacity: 0;
    transform: translateX(50px) scale(1);
  }
}

