#contact{
    nav{
        margin-top: 1rem;
        #logoid{
            display: flex;
            align-items: center;
            justify-content: flex-start;
            h1{
                text-decoration: none;
                font-size: 1.3rem;
                font-family: 'Allerta Stencil', sans-serif;
                font-weight: 400;
            }
        }
        #navlinkid{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            #productLink, #aboutLink, #contactLink{
                margin-right: 1rem;
                text-decoration: none;
                font-family: 'Allerta Stencil', sans-serif;
                color: $thirty;
                }
        }
    }
    #contactContent{
        #upfront{
            margin-top: 2rem;
            overflow: hidden;
            #back{
                position: relative;
                // border: 2px solid red;
                display: flex;
                align-items: center;
                justify-content: center;
                #backdrop{
                    font-size: 10rem;
                    text-transform: uppercase;
                    text-align: center;
                    // border: 2px solid red;
                    opacity: .1;
                    cursor: default;
                }
                #txt{
                    position: absolute;
                    top: 40%;
                    background: transparent;
                    cursor: default;
                    // border: 2px solid red;
                    #abouttext{
                        // border: 2px solid red;
                        background: transparent;
                        font-size: 3.5rem;
                    }
                }
            }
            
        }
    }
    #form--row{
        margin-top: 2.5rem;
        #form--first--col{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            input{
                width: 100%;
                min-height: 2rem;
                border-radius: 5px;
                border: 2px solid $thirty;
            }
        }
        #form--second--col{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            input{
                width: 100%;
                min-height: 2rem;
                border-radius: 5px;
                border: 2px solid $thirty;
            }
        }
        #form--third--col{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            margin-top: 1.8rem;
            textarea{
                width: 100%;
                border: 2px solid $thirty;
                border-radius: 5px;
            }
        }
        #form--fourth--col{
            display: flex;
            justify-content: flex-end;
            align-items: center;
            margin-top: 1rem;
            input{
                accent-color: $thirty;
            }
            label{
                margin-left: .3rem;
                margin-right: 1rem;
            }
        }
        #form--fifth--col{
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 1rem;
            button{
                width: 100%;
                min-height: 3rem;
                border: none;
                border-bottom-right-radius: 20px;         
                background: $thirty;
                color: $sixty;   
            }
        }
    }
    footer{
        margin-top: 1rem;
        border: 2px solid $thirty;
       
        div{
            border-top: 1px solid $sixty;
            background: $sixty;
            h3{
            font-family: 'Allerta Stencil', sans-serif;
            display: flex;
            align-items: center;
            background-color: $sixty;
            }
            div{
                border: none;
                // display: flex;
                // align-items: center;
                // flex-direction: column;
                // justify-content: flex-start;
                #one{
                    margin-top: 1rem;
                    text-align: right;
                    a{
                        background: $sixty;
                        margin-right: 1rem;
                        i{
                            font-size: 1.8rem;
                            background: $sixty;
                            color: $thirty;
                        }
                    }
                    #tweeta{
                        background: transparent;
                        #tweet{
                            color: $sixty;
                            background: $thirty
                        }
                    }
                }
                #two{
                    text-align: right;
                    font-weight: 500;
                }
            }
        }
    }
}