#theSlider{
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    div{
        background-color: $thirty;
        border-radius: 2rem;
        width: fit-content;
        height: fit-content;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            width: fit-content;
            height: 30rem;
            object-position: center;
            object-fit: contain;
            background: $thirty;
        }
    }
    h2{
        width: 45rem;
        font-size: 2.4rem;
        font-weight: 900;
        background: $thirty;
        color: $sixty;
        margin-top: .5rem;
    }
    h3{
        width: 45rem;
        font-size: .9rem;
        font-weight: 200;
        background: $thirty;
        color: $sixty;
        margin-bottom: 1rem;
    }
}